<template>
<v-flex xs2>
  <v-menu
    v-model="menu"
    offset-y
  >
    <v-btn
      slot="activator"
      dark
      icon
    >
      <v-icon>fa fa-globe-americas</v-icon>
    </v-btn>
    <v-list>
      <v-list-tile
        v-for="(item, index) in langs"
        :key="index"
        :class="language == item.value ? 'active_lang':''"
      >
      <a @click.prevent="language = item.value">
        <v-list-tile-title>{{ item.value }}</v-list-tile-title>
      </a>
      </v-list-tile>
    </v-list>
  </v-menu>
</v-flex>
</template>

<script>
export default {
  data () {
    return { 
      langs: [
        {value: 'en', text: 'en'},
        {value: 'es', text: 'es'}
      ],
      language: this.$i18n.locale,
      menu:false
    }
  },
  watch:{
    language(res){
      this.$validator.localize(res)
      this.$i18n.locale = res;
      localStorage.setItem("locale",res);
    }
  }
};
</script>