import Vue from 'vue'
import './plugins/vuetify'
import App from './main/App.vue'
import router from './router'
import store from './store'
import './registerServiceWorker'
import 'roboto-fontface/css/roboto/roboto-fontface.css'
import '@fortawesome/fontawesome-free/css/all.css'
import VueEditor from "vue2-editor"
import VueI18n from 'vue-i18n'
import axios from 'axios';
import VeeValidate, { Validator } from 'vee-validate'
import messages from 'vee-validate/dist/locale/es'
import {localeValue} from "./locale/locale.js"

Vue.use(VueI18n)
Validator.localize('es', messages)

Vue.config.productionTip = false
window.axios = axios;

let lang = 'en'

if(localStorage.getItem("token") != "" && localStorage.getItem("token") != null){
    window.axios.defaults.headers['Authorization'] = 'Token ' + localStorage.getItem('token');
}

if(localStorage.getItem("locale") != undefined){
    lang = localStorage.getItem("locale");
}
else{
    lang = navigator.language.split("-")[0];
}

Vue.use(VueEditor)
const i18n = new VueI18n({
  locale: lang, // set locale
  messages:localeValue // set locale messages
})

Vue.use(VeeValidate,{ locale:lang })

new Vue({
  router,
  store,
  i18n,
  render: h => h(App)
}).$mount('#app')
